import {getPollTop} from '../../api/api.js';
export default {
	data() {
		return {
			donateList: '',
			textColor: ['#4CD964', '#4CD964', '#4CD964']
		}
	},
	methods: {
		getDonateList() {
			getPollTop().then(data => {
				this.donateList = data;
			});
		}
	},
	onLoad() {
		this.getDonateList();
	}
}
